.feedback-form {
  max-width: 480px;
  background: #d0cece;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  background: green;
  border: 1px solid #ff0;
  border-radius: 15px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

